// footer

html, body {
  height: 100%;
}

.footer-v2 {

  .page {
    @extend %global_flex_block_column_nowrap_space_between_stretch;
  }

  .footer {
    position: relative;
    background-color: $color-dark;
    padding: 72px 0;
    font-family: $font-PN;
    color: #fff;
    overflow: hidden;
    flex: 0 0 auto;

    @media #{$mmedia-md} {
      padding: 56px 0;
    }

    a {
      color: #fff;

      &:hover {
        color: $color-red;
      }
    }

    .row {
      margin-left: -24px;
      margin-right: -24px;

      & > div {
        padding-left: 24px;
        padding-right: 24px;
      }
    }

    ul, ol {
      padding: 0;
      margin: 0;
      list-style: none;
    }

    &-copyright {
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      color: $color-white;
      padding: 10px 0 5px;

      @media #{$mmedia-sm} {
        margin-top: 40px;
        padding-top: 40px;
        border-top: 1px solid $color-grey;
      }
    }

    &-description {
      color: $color-white;
      font-size: 16px;
      line-height: 20px;

      &.fz-14 {
        font-size: 14px;
      }

      @media #{$mmedia-sm} {
        p + p {
          margin-top: 16px;
        }
      }

      @media #{$mmedia-mmd} {
        font-size: 14px;
      }
    }

    &-tab {

      @media #{$mmedia-mmd} {
        min-height: 155px;
      }
    }

    &-contacts {
      position: relative;
      font-family: $font-PNC;
      font-size: 32px;
      line-height: 1;

      @media #{$mmedia-md} {
        font-size: 24px;
      }
      @media #{$mmedia-sm} {
        font-size: 32px;
      }

      div {
        margin-top: 4px;

        &:first-child {
          margin-top: 0;
        }
      }

      a {
        text-decoration: none;
        color: $color-red;

        &:hover {
          color: #fff;
        }
      }
    }

    &-contacts {
      padding: 10px 0 36px;

      a {
        font: 24px/1 $font-PNC;

        @media #{$mmedia-mmd} {
          font-size: 32px;
        }

        @media #{$mmedia-sm} {
          font-size: 32px;
        }
      }

      p {
        margin-bottom: 4px;
      }

      @media #{$mmedia-sm} {
        padding-top: 48px;
      }
    }

    &-title {
      display: inline-block;
      padding: 4px 8px;
      border-radius: 4px;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 12px;
      line-height: 1.33;
      margin-bottom: 16px;

      &.grey {
        background-color: $color-grey;
      }

      &.red {
        background-color: $color-red;
      }
    }

    ul.footer-share {
      padding: 30px 0;

      li {
        display: inline-block;
        vertical-align: top;
        margin: 7px 16px 5px;

        @media #{$mmedia-sm} {
          margin: 15px 16px 8px;
        }
      }
    }

    &-share_link {
      font-size: 18px;
      text-decoration: none;
    }

    &-map_link {
      font-size: 14px;
      line-height: 16px;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }

      @media #{$mmedia-mmd} {
        font-size: 12px;
      }
    }

    &-line {
      position: absolute;
      top: -72px;
      bottom: -72px;
      left: 66.66667%;
      width: 1px;
      background: $color-grey;
      z-index: 1;
    }

    &-pwside {
      margin-bottom: -16px;

      .pwside-man {
        @extend %global_flex_block_row_nowrap_flex_start_center;
        display: inline-flex;
        width: auto;
        margin-right: 12px;
        margin-bottom: 16px;
      }

      .pwside-man_img {
        width: 20px;
        height: 20px;
        margin-right: 4px;

        @media #{$mmedia-md} {
          width: 24px;
          height: 24px;
        }
      }

      .pwside-man_text {
        color: #fff;
        text-decoration: none;
        letter-spacing: 0.02em;

        &:hover {
          color: $color-red;
        }

        @media #{$mmedia-sm} {
          font-size: 16px;
        }
      }
    }

    .navbar-item {
      flex: 1 0 auto;

      @media #{$mmedia-md} {
        &:not(.active) {
          flex: 1 0 50%;

          .navbar-tab {
            padding-left: 6px;
            font-size: 18px;

            &::before {
              width: 24px;
              height: 24px;
              font-size: 18px;
              line-height: 20px;
            }
          }
        }
      }

      &.active {
        border-radius: 0;

        @media #{$mmedia-md} {
          position: absolute;
          top: 0;
          right: 0;
          left: 0;
        }
      }
    }

    .navbar-list {
      @media #{$mmedia-md} {
        flex-flow: wrap;
        position: relative;
        padding-top: 135px;

        &.__no-active {
          .navbar-item:first-child {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
          }
        }
      }
    }

    .navbar-item.active {
      .navbar-tab {
        padding-left: 0;
        padding-right: 0;
        letter-spacing: .025em;
        height: 112px;

        &::before {
          margin-left: -5px;
        }
      }
    }

    .navbar-tab {
      white-space: nowrap;
      flex-flow: nowrap;
      border-radius: 0;
      height: 86px;
      letter-spacing: .025em;
      justify-content: center;

      @media #{$mmedia-sm} {
        transition: color .3s ease;
      }

      @media #{$mmedia-mmd} {
        height: 112px;
      }
    }

    &_tabs {

      @media #{$mmedia-mmd} {
        padding-top: 40px;
      }
    }

    &-link {
      @extend %global_flex_block_row_nowrap_flex_start_center;
      display: inline-flex;
      vertical-align: top;
      text-decoration: none;
      font-size: 16px;
      line-height: 1;
      letter-spacing: .015em;

      > span + span {
        padding-left: 4px;
      }

      @media #{$mmedia-msm} {
        font-size: 14px;
      }
    }

    &-caption {
      display: inline-block;
      vertical-align: top;
      border-radius: 4px;
      color: $color-default;
      background: #fff;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      padding: 4px;
      margin-top: 26px;
      margin-bottom: 22px;

      &.__red {
        background: $color-red;
        color: #fff;
      }

      @media #{$mmedia-mmd} {
        margin-bottom: 16px;
        margin-top: 16px;
      }
    }

    &-actions {
      //padding-bottom: 5px;

      li {
        //margin-bottom: 17px;
      }

      .footer-link {
        margin-right: 16px;

        @media #{$mmedia-sm} {
          margin-top: 6px;
          margin-bottom: 14px;
        }

        @media #{$mmedia-msm} {
          &:last-child {
            margin-bottom: 35px;
          }
        }
      }
    }

    &-action {

      &_name {
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        margin-bottom: 8px;
        color: $color-red;
      }

      &_info {
        display: inline-block;
        vertical-align: top;
        font-size: 14px;
        line-height: 20px;
        text-decoration: none;
        letter-spacing: .025em;
        margin-bottom: 17px;

        @media #{$mmedia-sm} {
          font-size: 16px;
        }
      }
    }

    &-col_up {
      @media #{$mmedia-mmd} {
        margin-top: -72px;
      }
    }

    &-yandex {
      position: relative;
      padding-left: 120px;
      font-size: 14px;
      line-height: 1.43;
      margin-top: 25px;

      @media #{$mmedia-mm} {
        padding-left: 0;
        padding-top: 30px;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 96px;
        height: 20px;
        background: url('../images/system/ya_logo.svg') 0 0 no-repeat;
        background-size: contain;
      }
    }
  }
}

.cookieapply {
  @include transition_all(transform .3s ease-in-out);
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100%;
  padding: 0 0 30px 0;
  z-index: 200;

  @media #{$mmedia-md} {
    padding-bottom: 20px;
  }

  &.cookiehide {
    bottom: -100% !important;
  }

  &-body {
    padding: 24px;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 32px rgba(0, 0, 0, .3);
    border-radius: 4px;
    background-color: $color-white;
  }

  &-img {
    margin-right: 16px;
    flex: 0 0 48px;
    max-width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$mmedia-md} {
      display: none;
    }
  }

  &-text {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    font-family: $font-PN;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.4;

    @media #{$mmedia-md} {
      width: 100%;
      margin-bottom: 24px;
    }
  }

  &-btn {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    margin-left: 24px;

    @media #{$mmedia-md} {
      margin-left: 0;
      width: 100%;

      .mbtn {
        display: block;
        width: 100%;
      }
    }
  }
}

.fz-21 {
  font-size: 21px;
  margin-top: -5px;
}

.ios * {
  cursor: pointer !important;
}
