.actions {
  position: relative;
  margin-top: -48px;

  &.row {
    @media #{$mmedia-mlg} {
      margin-left: -24px;
      margin-right: -24px;

      & > div {
        padding-left: 24px;
        padding-right: 24px;
      }
    }
  }

  &-el {
    margin-top: 48px;

    @media #{$mmedia-mlg} {
      padding-left: 24px;
      padding-right: 24px;
    }
    & + .actions-el + .actions-el {
      @media #{$mmedia-sm} {
        .actions-card-text {
          display: none;
        }
      }
    }
  }
  &-card {
    position: relative;

    &:hover {
      .actions-card-name {
        color: $color-red;
      }
    }

    &-img, picture {
      display: block;
      height: 336px;
      margin-bottom: 24px;
      border-radius: 4px;
      position: relative;
      z-index: 1;

      @media #{$mmedia-md} {
        height: 257px;
      }
      @media #{$mmedia-sm} {
        height: 257px;
        margin-bottom: 16px;
      }
      i {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-size: cover;
        background-color: #ddd;
        z-index: -1;
      }
      span {
        position: absolute;
        right: 16px;
        top: 16px;
        bottom: 16px;
        width: 240px;
        max-width: 100%;
        z-index: 2;

        @media #{$mmedia-lg} {
          width: 225px;
        }
        @media #{$mmedia-md} {
          width: 200px;
          justify-content: flex-end;
        }

        picture {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 100%;

          @media #{$mmedia-md} {
            justify-content: flex-end;
          }
        }
        img, picture {
          display: block;
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
    &-name {
      font-family: $font-PNC;
      font-size: 48px;
      letter-spacing: -0.75px;
      text-decoration: none;
      line-height: 1;
      position: relative;
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 16px;
      z-index: 3;

      @media #{$mmedia-md} {
        font-size: 40px;
      }
      @media #{$mmedia-sm} {
        font-size: 32px;
      }
    }
    &-text {
      font-weight: 300;
      position: relative;
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 16px;
    }
    &-header {
      position: absolute;
      left: 16px;
      top: 16px;
      z-index: 3;

      .lineinfo {
        margin-bottom: 16px;
      }
    }
    &-price {
      display: flex;
      align-items: center;
      color: #fff;

      &-label {
        width: 31px;
        height: 24px;
        background: url('../images/ic/after.svg') 50% 50% no-repeat;
        background-size: contain;
        margin-right: 8px;
      }
      &-number {
        font-family: $font-PNC;
        font-size: 48px;
        line-height: 1;
        letter-spacing: -2px;

        @media #{$mmedia-md} {
          font-size: 40px;
        }
        @media #{$mmedia-sm} {
          font-size: 32px;
        }
      }
    }
  }
}
