.search {
  position: relative;
  margin-bottom: 40px;

  @media #{$mmedia-md} {
    margin-bottom: 32px;
  }

  &-wrapper + .block-padd {
    padding-top: 0;
  }
  .layoutwhite {
    z-index: 100;
  }

  &.searchinside {
    margin-bottom: 0;
    width: 288px;

    &.searchanimate {
      .search-body {
        width: 1108px;

        @media #{$mmedia-lg} {
          width: 928px;
        }
      }
      .search-button {
        margin-right: 0;
        opacity: 1;
        visibility: visible;
        z-index: 1;
      }
    }
    .search-body {
      position: absolute;
      width: 288px;
      top: 0;
      right: 0;
      z-index: 100;
      transition: all 0.5s ease;
    }
    .search-button {
      margin-right: -230px;
      opacity: 0;
      visibility: hidden;
      z-index: -1000;
      transition: all 0.5s ease;
    }

    .search-mobiletoggle,
    .search-info {
      display: none !important;
    }
  }
  &.searchresult {
    margin-bottom: 32px;
    margin-top: 40px;

    @media #{$mmedia-md} {
      margin-bottom: 24px;
      margin-top: 32px;

      .search-button {
        display: block;
        flex: 0 0 100px;
        max-width: 100px;
        margin-left: 8px;

        .mbtn {
          padding-top: 12px;
          padding-bottom: 12px;
        }
      }
    }

    .search-body {
      flex-wrap: nowrap;
      display: flex !important;

      @media #{$mmedia-md} {
        display: none !important;
      }
    }
    .search-mobiletoggle {
      color: $color-grey;
    }
    .search-field {
      margin-bottom: 0;
    }
    .search-status {
      top: -3px;
    }

  }
  &.active {
    @media #{$mmedia-mmd} {
      z-index: 2000;
    }

    .search-field {
      @media #{$mmedia-mmd} {
        border-color: $color-red;
        box-shadow: 0 8px 16px transparentize($translucent-red, .84);
      }
    }
    .layoutwhite  {
      @media #{$mmedia-mmd} {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  form {
    display: block;
  }

  &-body {
    display: flex;
    position: relative;
    z-index: 199;

    @media #{$mmedia-md} {
      display: none;
    }
  }
  &-title {
    font-family: $font-PNC;
    font-size: 32px;
    line-height: 1;
    letter-spacing: -0.75px;
    color: $color-default;
    margin-bottom: 40px;
  }
  &-info {
    position: relative;
    padding-top: 16px;
    font-family: $font-PN;
    font-size: 14px;
    line-height: 1.43;
    text-transform: uppercase;

    a {
      color: $color-red;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
  &-input {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
  }
  &-field {
    outline: none;
    background: #fff url('../images/ic/loop.svg') 16px 50% no-repeat !important;
    background-size: auto 18px;
    border: 1px solid $color-grey;
    border-radius: 4px !important;
    width: 100%;
    font-family: $font-PN;
    font-size: 18px;
    color: $color-grey;
    line-height: 1.45;
    padding: 14px 16px 14px 41px;
    box-shadow: 0 8px 16px transparentize($translucent-default, .9);

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 1.5;
      padding: 12px 16px 12px 41px;
      background-size: auto 16px;
      margin-bottom: 28px;
    }

    &::placeholder {
      color: $color-light;
      opacity: 1;
    }
    &:focus {
      border-color: $color-red;
      box-shadow: 0 8px 16px transparentize($translucent-red, .84);
    }
  }
  &-status {
    position: absolute;
    top: 0;
    right: 0;
    height: 56px;
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;

    @media #{$mmedia-md} {
      top: 69px;
    }

    &.active {
      display: flex;
    }
    & > div {
      display: none;

      &.active {
        display: block;
      }
    }
  }
  &-delete {
    outline: none;
    padding: 0;
    border: none;

    .sicon {
      width: 16px;
      height: 16px;
    }
  }
  &-button {
    flex: 0 0 214px;
    max-width: 214px;
    margin-left: 16px;

    @media #{$mmedia-md} {
      display: none;
    }
  }
  &-popup {
    position: absolute;
    left: 0;
    right: 0;
    top: 64px;
    display: none;
    font-family: $font-PN;
    z-index: 10;
  }
  &-pbody {
    background-color: #fff;
    border: 1px solid $color-red;
    box-shadow: 0 8px 16px transparentize($translucent-red, .84);
    border-radius: 4px;
    overflow: hidden;
  }
  &-pcontent {
    padding: 20px 16px;
  }
  &-pfoot {
    padding: 16px;
    border-top: 1px solid $color-red;

    @media #{$mmedia-md} {
      padding: 16px 0 0 0;
      border: none;
    }
  }
  &-pitem {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
  &-welcome {
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.71;
    color: $color-grey;
    font-family: $font-default;
    margin-bottom: 20px;
  }
  &-mw560 {
    max-width: 100%;
  }
  &-loading {
    width: 20px;
    height: 20px;

    img {
      display: block;
    }
  }
  &-sup {
    width: 16px;
    height: 16px;
    align-items: center;
    justify-content: center;

    img {
      display: block;
    }
  }

  .blockscroll {
    margin-top: -20px;
    margin-bottom: -20px;
  }
  .blockscroll-fix {
    max-height: 280px;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &-mobiletoggle {
    outline: none !important;
    display: block;
    width: 100%;
    padding: 12px 16px 12px 40px;
    border: 1px solid $color-grey;
    background: #fff url('../images/ic/loop.svg') 16px 50% no-repeat;
    background-size: auto 16px;
    border-radius: 4px;
    font-family: $font-PN;
    font-size: 16px;
    line-height: 1.5;
    color: $color-light;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-decoration: none;
    text-overflow: ellipsis;
    box-shadow: 0 8px 16px transparentize($translucent-default, .9);
  }
  &-mobileclose {
    outline: none !important;
    padding: 0;
    border: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 60px;
    height: 64px;
    background: $color-red url('../images/ic/close.svg') 50% 50% no-repeat;
    z-index: 10;

    &:hover {
      background-color: $color-redlight;
    }
  }
  &-notfound {
    position: relative;

    &-title {
      font-weight: 300;
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 24px;
      max-width: 688px;

      @media #{$mmedia-md} {
        font-size: 20px;
        line-height: 32px;
        margin-bottom: 18px;
      }
    }
  }
}
