//Merriweather
//@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;1,400&display=swap');


@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather-Italic.eot");
  src: local("Merriweather Italic"), local("Merriweather-Italic"),
  url("../fonts/Merriweather-Italic.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Merriweather-Italic.ttf") format("truetype"),
  url("../fonts/Merriweather-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather-LightIt.eot");
  src: local("Merriweather Italic"), local("Merriweather-LightIt"),
  url("../fonts/Merriweather-LightIt.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Merriweather-LightIt.ttf") format("truetype"),
  url("../fonts/Merriweather-LightIt.woff") format("woff");
  font-weight: 300;
  font-style: italic;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather-Regular.eot");
  src: local("Merriweather Regular"), local("Merriweather-Regular"),
  url("../fonts/Merriweather-Regular.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Merriweather-Regular.ttf") format("truetype"),
  url("../fonts/Merriweather-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather-Bold.eot");
  src: local("Merriweather Bold"), local("Merriweather-Bold"),
  url("../fonts/Merriweather-Bold.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Merriweather-Bold.ttf") format("truetype"),
  url("../fonts/Merriweather-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather-Light.eot");
  src: local("Merriweather Light"), local("Merriweather-Light"),
  url("../fonts/Merriweather-Light.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Merriweather-Light.ttf") format("truetype"),
  url("../fonts/Merriweather-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

$font-default: 'Merriweather', serif;
$font-PN: "proxima-nova", sans-serif;
$font-PNC: "proxima-nova-condensed", sans-serif;
$second_font: $font-default;
$second-font: $font-default;
$proxima_nova: $font-PN;

.tk-merriweather { font-family: $font-default; }

// Proxima Nova and Proxima Nova Condensed
.tk-proxima-nova { font-family: $font-PN; }
.tk-proxima-nova-condensed { font-family: $font-PNC; }

