.education {

  &-holder {
    @media #{$mmedia-md} {
      @include flex_block(column nowrap, flex-start, stretch);

      .education-footer.__top {
        order: -1;
        border-bottom: 1px solid #000;

        &::before {
          display: none;
        }
      }
    }
  }

  &-footer {
    padding: 14px 0;
    margin-bottom: 5px;

    &_link {
      @extend %global_flex_block_row_nowrap_flex_start_center;
      text-decoration: none;
      font-size: 16px;
      line-height: 1.5;
      letter-spacing: .015em;

      > span {
        padding-right: 10px;
        line-height: 1;
      }

      &:hover {
        color: $color-red;
      }
    }

    @media #{$mmedia-md} {
      position: relative;
      padding: 15px 0;
      margin-bottom: 24px;

      &::before {
        content: '';
        position: absolute;
        top: -22px;
        right: 0;
        left: 0;
        border-top: 1px solid $color-grey;
      }
    }

    @media #{$mmedia-mmd} {
      margin: 20px 0 7px;
      padding: 28px 15px;
      border-top: 1px solid #000;
      border-bottom: 1px solid #000;

      &_link {
        font-size: 18px;
      }
    }
  }

  &-el {
    position: relative;
    padding-bottom: 34px;

    .main-content & {
      margin-top: 34px;
      padding-bottom: 26px;

      &.__schedule {
        @media #{$mmedia-md} {
          margin-top: 28px;

          .education-card_text {
            font-size: 24px;
            line-height: 24px;
          }

          span.icon-camera {
            font-size: 24px;
          }
        }
      }

      &.__community {
        .filter-result {
          @media #{$mmedia-md} {
            padding-bottom: 14px;
          }

          @media #{$mmedia-mmd} {
            margin-bottom: 18px;
          }
        }

        .education-card.__premium .filter-result {
          @media #{$mmedia-mmd} {
            margin-bottom: 10px;
          }
        }
      }
    }

    .main-content .community-list & {
      &.__community {
        @media #{$mmedia-md} {
          margin-top: 50px;
        }
      }
    }

    .navbar & {
      &::before {
        content: '';
        position: absolute;
        top: -14px;
        right: 16px;
        left: 16px;
        border-top: 1px solid $color-grey;

        @media #{$mmedia-md} {
          .scroll-content > .row & {
            //right: 0;
            //left: 0;
          }
        }
      }
    }

    &:first-child {
      &::before {
        display: none;
      }
    }

    &.__direction {
      &:first-child {
        margin-top: 10px;

        &::before {
          display: none;
        }
      }

      @media #{$mmedia-md} {
        padding-bottom: 40px;

        .scroll-content > .row & {
          .education-school {
            //margin-left: -16px;
            //margin-right: -16px;
          }
        }

        &::before {
          top: -21px;
        }

        &:first-child {
          margin-top: 20px;
        }
      }
    }

    &.__school {
      &:first-child {
        margin-top: 10px;

        &::before {
          display: none;
        }
      }

      @media #{$mmedia-md} {
        padding-bottom: 25px;

        .scroll-content > .row & {
          .education-school {
            //margin-left: -16px;
            //margin-right: -16px;
          }
        }

        &::before {
          top: -14px;
        }

        &:first-child {
          margin-top: 12px;
        }
      }
    }

    .catalogselections-side {
      border: none;
    }

    @media #{$mmedia-mmm} {
      .pwside {
        display: block;
      }

      .pwside-side {
        margin-right: 0;
      }

      .pwside-man {
        //width: auto;
      }

      .navbar & {
        &.col-sm-6 {
          &:first-child,
          &:nth-child(2) {
            margin-top: 10px !important;

            &::before {
              display: none;
            }
          }
        }
      }
    }

    @media #{$mmedia-msm} {
      .navbar & {
        &.col-md-4 {
          //&.__direction,
          //&.__school {
          &:nth-child(2),
          &:nth-child(3) {
            margin-top: 10px !important;
          }

          &::before {
            display: none;
          }

          //}
        }
      }
    }

    @media #{$mmedia-md} {
      .navbar & {
        &.__schedule {
          padding-bottom: 25px;
          margin-top: 15px;

          .scroll-content > .row & {
            .education-card {
              //margin-left: -16px;
              //margin-right: -16px;
            }
          }

          .catalogselections-item {
            padding-left: 48px;
          }

          .scroll-content > .row & {
            .catalogselections-item {
              //margin-left: -16px;
              //margin-right: -16px;
            }
          }

          .catalogselections-body {

          }

          .catalogselections-side {
            padding: 0;
          }

          .pwside-man {
            margin: 0;
            text-align: left;
          }

          .pwside-man_img {
            position: absolute;
            top: 8px;
            left: 0;
            width: 40px;
            height: 40px;
          }

          .pwside-man_name {
            text-transform: uppercase;
            font-size: 16px;
            line-height: 20px;
            margin-top: -4px;
            margin-bottom: 8px;
          }

          .pwside-man_text {
            font-size: 16px;
            line-height: 20px;
          }

          .ltitle.grey {
            background: none;
            padding: 0;
            margin: 0;
            font-weight: bold;
            font-size: 12px;
            line-height: 16px;
            color: $color-light;
          }
        }
      }
    }

    &.__community {

      @media #{$mmedia-md} {
        .pagecard-cashback-info-more {
          font-size: 12px;
          line-height: 16px;
        }
      }
    }
  }

  &-school {
    @extend %global_flex_block_row_nowrap_flex_start_center;
    text-decoration: none;
    //display: inline-flex;

    &_info {
      line-height: 1;
    }

    &_logo {
      @include transition(background-color, .3s);
      width: 96px;
      height: 96px;
      border-radius: 100%;
      overflow: hidden;
      margin-right: 16px;
      position: relative;
      flex: 0 0 auto;

      img {
        display: inline-block;
        vertical-align: middle;
        max-width: 100%;
        max-height: 100%;
      }

      .education-el.__school & {

        @media #{$mmedia-sm} {
          width: 56px;
          height: 56px;
          margin-right: 6px;
        }
      }

      .education-el.__direction & {
        width: 64px;
        height: 64px;
        background: $color-red;

        @media #{$mmedia-sm} {
          width: 40px;
          height: 40px;
          margin-right: 6px;
        }
      }

      .education-card & {
        width: 32px;
        height: 32px;
        margin-right: 6px;
      }

      .main-content .education-card & {
        width: 48px;
        height: 48px;
      }
    }

    &_location {
      text-transform: uppercase;
      font: 700 12px/16px $font-PN;
      display: block;
      color: $color-light;


    }

    &_name {
      @include transition(color, .3s);
      text-transform: uppercase;
      font: 16px/20px $font-PN;
      padding-top: 3px;
      position: relative;
      z-index: 1;

      .education-card & {
        font-size: 12px;
        line-height: 16px;

        @media #{$mmedia-mmd} {
          font-size: 14px;
          line-height: 20px;
        }
      }

      .main-content .education-card.__premium & {
        font-size: 14px;
        line-height: 20px;

        @media #{$mmedia-mmd} {
          font-size: 18px;
          line-height: 24px;
        }
      }
    }

    &.__small {
      .education-school_logo {
        width: 32px;
        height: 32px;
        margin-right: 4px;
      }


    }

    &.__community {
      margin-bottom: 16px;

      @media #{$mmedia-md} {
        .education-school_logo {
          width: 24px !important;
          height: 24px !important;
          margin-right: 8px;
        }
      }

      .education-school_name {
        font-weight: 700;
        text-transform: none;
        font-size: 12px;
        line-height: 12px;

        @media #{$mmedia-mmd} {
          font-size: 18px;
          line-height: 28px;
          padding-left: 6px;
        }
      }
    }

    &:hover {
      .education-school_logo {
        background-color: transparent;
      }

      .education-school_name {
        color: $color-red;
      }
    }
  }

  &-card {
    position: relative;
    display: block;
    text-decoration: none;

    &_mute {
      @extend %icon_pseudo !optional;
      @include transition_all(color .3s);
      position: absolute;
      bottom: 20px;
      right: 20px;
      z-index: 4;
      width: 32px;
      height: 32px;
      cursor: pointer;
      color: #fff;
      text-align: center;

      &:after {
        content: '';
        position: absolute;
        top: -16px;
        bottom: -16px;
        left: -16px;
        right: -16px;
        background: rgba(0, 0, 0, 0.00001);
      }

      &:hover {
        color: $color-red;
      }

      &:before {
        content: $icon-sound;
      }

      &.muted:before {
        content: $icon-muted;
      }

      .ended & {
        display: none;
      }

      @media #{$mmedia-md} {
        bottom: 10px;
        right: 10px;
      }
    }

    &_stick {
      position: relative;

      @media #{$mmedia-mmd} {
        height: 100%;
        padding-top: 8px;

        .education-card_slider {
          padding-bottom: 56px;
        }
      }
    }

    &_slider {
      position: relative;
      margin-bottom: 20px;

      &:not(.sticky) {
        margin-top: 0 !important;
      }

      &.sticky {
        z-index: 6;
        transition: 0s !important;
        transform: none !important;

        @media #{$mmedia-md} {
          //padding-top: calc(var(--currentVH) - 160px);
        }

        .container {
          box-shadow: 0 8px 16px rgba(44, 44, 44, 0.1);
        }

        &:not(.sticky-end) {
          @media #{$mmedia-md} {
            .container .scroll-content {
              //  transform: translateY(calc(var(--currentVH) - 160px)) !important;
              //  margin-top: calc(var(--currentVH) - 160px) !important;
            }
          }

          .brandsalph > li > a {
            line-height: 48px;

            @media #{$mmedia-md} {
              line-height: 56px;
            }
          }
        }
      }

      .swiper-slide.ended {
        video {
          display: none;
        }
      }

      @media #{$mmedia-mmd} {
        margin-bottom: 16px;
      }

      .scroll-fixed & {
        margin-top: calc(-0.5 * var(--currentVH));
      }

      .education-card-img {
        margin-bottom: 0 !important;
      }

      .swiper-button {
        @include transition_all(background-color .3s);
        position: absolute;
        top: 50%;
        margin-top: -16px;
        width: 32px;
        height: 32px;
        outline: none;
        border: none;
        padding: 0;
        z-index: 4;
        border-radius: 50%;
        cursor: pointer;

        &:before {
          content: '';
          position: absolute;
          top: -16px;
          bottom: -16px;
          left: -16px;
          right: -16px;
          background: rgba(0, 0, 0, 0.00001);
        }

        &.swiper-button-disabled {
          background-color: $color-light !important;
        }

        &:not(.swiper-button-disabled):hover {
          background-color: $color-default;
        }

        &.swiper-button-prev {
          left: 16px;
          background: $color-red url('../images/ic/prev-sl.svg') 10px 50% no-repeat;
          background-size: auto 14px;
        }

        &.swiper-button-next {
          right: 16px;
          background: $color-red url('../images/ic/next-sl.svg') 12px 50% no-repeat;
          background-size: auto 14px;
        }
      }
    }

    &_date {
      @include transition(color);
      @extend %global_flex_block_row_nowrap_flex_start_center;
      font: 24px/1 $font-PNC;
      margin-bottom: 4px;
      letter-spacing: -.01em;
      color: $color-default;
      white-space: nowrap;

      .navbar-submenu & {
        cursor: pointer;
      }

      .main-content .__premium & {
        font-size: 32px;
        line-height: 1;
        letter-spacing: -0.01em;

        span.icon-camera {
          font-size: 22px;
          margin-top: -7px;
          margin-left: 7px;
        }

        span.icon-arrow_right {
          font-size: 22px;
          margin: -7px 7px 0;
        }
      }

      span {
        &[class^="icon-"], &[class*=" icon-"] {
          margin: -3px 4px 0;
        }

        &.icon-arrow_right {
          font-size: 16px;
        }

        &.icon-camera {
          margin-left: 7px;

          @media #{$mmedia-mmd} {
            font-size: 24px;
          }
        }
      }

      @media #{$mmedia-mmd} {
        font-size: 32px;

        .main-content .__premium & {
          font-size: 48px;
          line-height: 1;
          margin-bottom: 10px;
          letter-spacing: -0.03em;

          span.icon-camera {
            font-size: 30px;
            margin-top: -7px;
            margin-left: 14px;
          }

          span.icon-arrow_right {
            font-size: 22px;
            margin: -7px 14px 0;
          }
        }
      }

      .main-content & {
        font-size: 32px;
      }

      .main-content .education-card_header.__short & {
        @media #{$mmedia-md} {
          font-size: 18px;
          line-height: 18px;
        }
      }

      .education-card:hover & {
        color: $color-red;
      }
    }

    &_location {
      color: $color-light;
      font: 12px/1 $font-PN;
      text-transform: uppercase;
      margin-bottom: 10px;

      .main-content & {
        margin-bottom: 15px;

        @media #{$mmedia-mmd} {
          margin-bottom: 20px;
        }
      }

      .education-el.__schedule & {
        @media #{$mmedia-md} {
          font-size: 14px;
          line-height: 16px;
          margin-bottom: 18px;
        }
      }

      .main-content .__premium & {
        font-size: 14px;

        @media #{$mmedia-mmd} {
          font-size: 16px;
          margin-bottom: 25px;
        }
      }

      .main-content .education-card_header.__short & {
        @media #{$mmedia-md} {
          font-size: 12px;
          line-height: 16px;
        }
      }
    }

    &_text {
      @include transition(color);
      font: 700 16px/ 20px $font-PN;
      margin-bottom: 3px;
      color: $color-default;
      text-decoration: none;
      display: inline-block;
      vertical-align: top;

      .navbar-submenu & {
        font-weight: 400;
        cursor: pointer;
      }

      .__premium & {
        font-size: 24px;
        line-height: 1;

        @media #{$mmedia-md} {
          margin-bottom: 16px;
        }
      }

      @media #{$mmedia-md} {
        .main-content .education-card:not(.__square) & {
          font-size: 24px;
          line-height: 1;
          margin-bottom: 15px;
        }
      }

      @media #{$mmedia-mmd} {
        font-size: 18px;
        line-height: 28px;

        .main-content & {

          .__premium & {
            font-size: 32px;
          }
        }
      }

      @media #{$mmedia-mlg} {
        .__premium & {
          font-size: 48px;
        }
      }

      .main-content & {
        margin-bottom: 15px;
      }

      .education-card:hover & {
        color: $color-red;
      }
    }

    &_periods {
      display: flex;
      flex-flow: row wrap;
      margin: 0 -10px 0 0;
      padding: 10px 0 0;
      list-style: none;

      li {
        color: $color-light;
        position: relative;
        margin: 6px 10px 0 0;
        border-radius: 3px;
        border: 1px solid transparent;
        font: 14px/20px $font-PN;

        &.active {
          padding-left: 3px;
          padding-right: 3px;
          border: 1px solid $color-light;
        }

        .soon {
          position: absolute;
          top: -12px;
          left: 11px;
          padding: 4px;
          background: $color-red;
          color: #fff;
          height: 24px;
          border-radius: 4px;
          font-weight: bold;
          font-size: 10px;
          line-height: 16px;
          text-transform: uppercase;

          @media #{$mmedia-md} {
            top: -10px;
            left: 9px;
            height: 20px;
            font-size: 10px;
            line-height: 12px;
          }
        }
      }

      &.__large {
        overflow-y: hidden;
        overflow-x: auto;
        flex-flow: row nowrap;

        &::-webkit-scrollbar {
          width: 0;
          height: 0;
        }

        li {
          white-space: nowrap;
          text-transform: uppercase;
          padding: 15px 18px;
          font-size: 14px;
          line-height: 20px;
          margin-bottom: 15px;

          @media #{$mmedia-md} {
            margin-top: 14px;
            margin-bottom: 0;
            font-size: 12px;
            padding: 12px 8px;
          }
        }

      }
    }

    &-img {
      display: block;
      height: 170px;
      margin-bottom: 16px;

      img {
        display: inline-block;
        vertical-align: top;
      }

      .main-content & {

        @media #{$mmedia-mmd} {
          margin-bottom: 7px;
          height: 256px;
        }

        @media #{$mmedia-md} {
          height: auto;
          margin-bottom: 8px;

          img {
            width: 100%;
          }
        }
      }

      .main-content .education-card.__square & {
        position: relative;
        height: auto;
        overflow: hidden;
        z-index: 1;

        &:before {
          content: '';
          display: inline-block;
          vertical-align: top;
          width: 100%;
          padding-bottom: 100%;
        }

        video,
        img {
          @extend %hor_vert_centr;
          position: absolute;
          min-width: 100%;
        }

        img {
          max-height: 100%;
        }

        video {
          &.fit-height {
            height: 100%;
          }

          &.fit-width {
            width: 100%;
          }
        }
      }

      .main-content .education-el.__big & {
        margin-bottom: 20px;

        @media #{$mmedia-mmd} {
          margin-bottom: 16px;
        }
      }

      .main-content .education-card.__premium & {
        margin-bottom: 12px;

        @media #{$mmedia-mmd} {
          margin-bottom: 16px;
        }
      }
    }

    &:hover {
      z-index: 6;

      .pcard-popup {
        bottom: -62px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &-logo_school {
    display: inline-block;
    vertical-align: top;

    img {
      max-width: 100%;
      display: block;
    }

    @media #{$mmedia-md} {
      margin: 16px 0;

      img {
        max-height: 56px;
      }
    }
  }
}

.color-red {
  color: $color-red;
}

.color-green {
  color: $color-green;
}

.fz-md-32 {
  @media #{$mmedia-mmd} {
    font-size: 32px;
    line-height: 1;
  }
}

.fz-lg-48 {
  @media #{$mmedia-mlg} {
    font-size: 48px;
    line-height: 1;
  }
}


