.effect-rotate {
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  100% {
    transform:rotate(360deg);
  }
}

@keyframes stroke {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotateminus {
  100% {
    transform: rotate(-360deg);
  }
}
@keyframes spincart {
  0% {
    background-position: 0, 0 0;
    -webkit-transform: rotateX(0deg);
  }
  100% {
    background-position: 0, 0 -640px;
    -webkit-transform: rotateX(360deg);
  }
}