.modal-backdrop {
  background-color: #fff;

  &.show {
    opacity: .8;
  }
}
.modal {
  cursor: url('../images/ic/closemodal.svg') 0 0, auto;

  .modal-body {
    cursor: auto;
  }
}

.mchcity {
  .modal-dialog {
    margin-top: 16px;

    @media #{$mmedia-mmd} {
      max-width: 608px;
      margin-top: 152px;
    }
  }
  .modal-content {
    background-color: $color-default;
    border: none;
    border-radius: 4px;
    box-shadow: 0 0 32px rgba(0,0,0,.3);
    color: #fff;
    font-family: $font-PN;
    font-size: 16px;
    line-height: 1.4;
  }
  .modal-body {
    padding: 24px 40px;

    @media #{$mmedia-md} {
      padding: 24px 16px;
    }
  }

  &-title {
    font-family: $font-PNC;
    font-size: 32px;
    font-weight: 900;
    line-height: 1;
    margin-bottom: 4px;
    color: $color-line;
  }
  &-text {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.75;
    font-family: $font-default;
    margin-bottom: 24px;
    color: #fff;
  }
  .footer-title {
    margin-bottom: 8px;
  }
  &-city {
    list-style: none;
    margin: -4px -10px 20px -10px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    font-size: 16px;
    line-height: 24px;

    & > li {
      padding: 4px 10px;

      & > a {
        display: block;
        white-space: nowrap;
        color: #fff;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 0.3px;

        &:hover {
          color: $color-red;
          text-decoration: none;
        }
      }
    }
  }
  &-form {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;

    &-body {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;

      @media #{$mmedia-md} {
        width: 100%;
      }

      .blockscroll {
        margin-top: -20px;
        margin-bottom: -20px;

        &::after {
          background: linear-gradient(180deg, rgba(44,44,44,0) 0%, rgba(44,44,44,1) 100%);
        }
        &::before {
          background: linear-gradient(to top, rgba(44,44,44,0) 0%, rgba(44,44,44,1) 100%);
        }
      }
      .blockscroll-fix {
        max-height: 260px;
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }
    &-btn {
      flex: 0 0 168px;
      max-width: 168px;
      margin-left: 16px;

      @media #{$mmedia-md} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-top: 16px;
        margin-left: 0;
      }
      
      button {
        display: block;
        outline: none !important;
        border: 1px solid $color-red;
        background-color: $color-red;
        color: #fff;
        font-family: $font-PN;
        font-weight: bold;
        font-size: 16px;
        text-transform: uppercase;
        line-height: 24px;
        padding: 15px 6px;
        border-radius: 4px;
        width: 100%;

        &:hover {
          border-color: $color-red;
          background-color: $color-red;
        }
        &:disabled {
          border: 1px solid $color-light;
          background-color: $color-light;
          cursor: default;
        }
      }
    }
    .search-status {
      right: 0;
    }
  }
}

.minfo {

  &.mbg-red {
    .modal-content {
      background-color: $color-red;
    }
  }
  &.mbg-green {
    .modal-content {
      background-color: $color-green;
    }
  }
  .modal-dialog {
    align-items: flex-end;
    margin: 0;
    min-height: calc(100vh - 132px);
    width: 100%;

    @media #{$mmedia-mmm} {
      max-width: 484px;
    }
    @media #{$mmedia-msm} {
      margin-left: auto;
      margin-right: auto;
    }
  }
  .modal-content {
    background-color: $color-default;
    border: none;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 0 32px rgba(0,0,0,.3);
    color: #fff;
    font-family: $font-PN;
    font-size: 16px;
    line-height: 1.4;
    margin: 32px 16px 100px 16px;

    @media #{$mmedia-msm} {
      margin-left: auto;
      margin-right: auto;
    }
    
    a {
      color: #fff;
      &:hover {
        color: #fff;
      }
    }
  }
  .modal-body {
    padding: 40px;

    @media #{$mmedia-md} {
      padding: 16px;
    }
  }

  &.fade .modal-dialog {
    transform: none;

    .modal-content {
      margin-bottom: 0;
      transition: all 0.3s ease;
    }
  }
  &.show .modal-dialog {
    .modal-content {
      margin-bottom: 100px;
    }
  }

  &-btns {
    display: flex;
    flex-wrap: wrap;
    margin: 16px -8px -8px -8px !important;

    & > div {
      padding: 8px !important;
    }
    .mbtn {
      white-space: nowrap;

      @media #{$mmedia-mmd} {
        min-width: 134px;
      }
      @media #{$mmedia-md} {
        display: block;
        width: 100%;
      }
    }
    .mbtn-primary {
      @media #{$mmedia-mmd} {
        min-width: 201px;
      }
    }
  }
  &-title {
    padding: 0 0 0 69px;
    min-height: 62px;
    font-size: 18px;
    line-height: 20px;
    font-weight: bold;
    margin-bottom: 8px;
    background: url('../images/ic/ic-m-warning.svg') 0 50% no-repeat;
    display: flex;
    align-items: center;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 18px;
    }
  }
  &-text {
    position: relative;
    font-size: 16px;
    line-height: 1.3;

    @media #{$mmedia-md} {
      font-size: 14px;
    }

    p {
      margin-bottom: 8px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &-bonus {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -8px 8px -8px;

    & > div {
      padding: 0 8px;
    }
    &-price {
      font-family: $font-PNC;
      font-size: 48px;
      line-height: 1;
      letter-spacing: -0.75px;
      font-weight: bold;
      padding-right: 47px;
      background: url('../images/system/smarket-bonus.svg') 100% 2px no-repeat;
      background-size: auto 39px;
      position: relative;

      &.sale {
        padding-left: 48px;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: -3px;
          width: 48px;
          height: 48px;
          background: url('../images/system/grafsalebonus.svg') 50% 50% no-repeat;
          background-size: auto 26px;
        }
      }
    }
    &-priceold {
      font-family: $font-PNC;
      font-size: 32px;
      line-height: 1;
      letter-spacing: -0.4px;
      font-weight: 300;
      padding-right: 29px;
      background: url('../images/system/smarket-bonus.svg') right 1px top 2px no-repeat;
      background-size: auto 24px;
      text-decoration: line-through;
    }
  }

  &-action {
    margin-bottom: 24px;
  }
  &-images {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: $color-grey;
      opacity: .7;
      z-index: 3;
    }
    &_bg {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-position: 50% 50% !important;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      z-index: 1;
    }
    &_good {
      position: absolute;
      left: 16px;
      top: 16px;
      right: 16px;
      bottom: 16px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      z-index: 2;

      img {
        display: block;
        align-items: center;
        justify-content: center;
        max-width: 100%;
        max-height: 100%;
      }
    }

    & + .modal-body {
      position: relative;
      padding-top: 24px;
      padding-bottom: 24px;
      z-index: 2;

      .minfo-btns {
        margin-top: 16px;
      }
    }
  }
}

.msignup {
  .modal-dialog {
    @media #{$mmedia-mmm} {
      max-width: 608px;
    }
  }
  .modal-content {
    border: none;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 0 32px rgba(0,0,0,.3);
    background-color: $color-default;
  }
  .modal-body {
    padding: 32px 40px;

    @media #{$mmedia-sm} {
      padding: 24px 16px;
    }
  }
  &-title {
    padding: 40px;
    background: $color-red url('../images/system/bg-mtitle.jpg') 50% 50% no-repeat;
    color: #fff;
    font-family: $font-PNC;
    font-weight: 900;
    font-size: 32px;
    line-height: 32px;

    @media #{$mmedia-sm} {
      padding: 24px 16px;

    }
  }
  &-wtext {
    font-family: $font-default;
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    margin-bottom: 24px;

    @media #{$mmedia-sm} {
      font-size: 14px;
      line-height: 24px;
    }
  }

  &-wslide {
    position: relative;

    &.active {
      .msignup-wslide-title {
        color: $color-light;

        &::after {
          background-color: $color-light;
          transform: rotate(180deg);
        }
      }
    }
    &-title {
      display: block;
      position: relative;
      padding-left: 24px;
      font-size: 16px;
      line-height: 24px;
      text-decoration: underline;
      color: #fff;

      &:hover {
        color: $color-light;
        text-decoration: underline;

        &::after {
          background-color: $color-light;
        }
      }
      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -8px;
        width: 16px;
        height: 16px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: contain !important;
        mask-image: url('../images/ic/arrow-white.svg');
        background-color: #fff;
      }
    }
    &-slide {
      display: none;
      font-size: 14px;
      line-height: 20px;
      padding-top: 8px;

      p {
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &-btns {
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    ul {
      list-style: none;
      margin: -4px -12px;
      padding: 0;
      position: relative;
      display: flex;
      flex-wrap: wrap;

      & > li {
        padding: 4px 12px;
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
      }
    }
  }

  &-rinfo {
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 24px;

    @media #{$mmedia-sm} {
      font-size: 14px;
      line-height: 20px;
    }

    & > span {
      display: block;
      font-size: 18px;
      line-height: 28px;
      padding: 8px 16px;
      margin: 0 -16px 8px -16px;
      color: $color-default;
      background-color: $color-line;
      border-radius: 4px;

      @media #{$mmedia-sm} {
        margin-left: 0;
        margin-right: 0;
        font-size: 14px;
        line-height: 20px;
      }
    }
    p {
      margin-bottom: 8px;

      &:last-child {
        margin-bottom: 0;
      }
      a {
        color: #fff;
        text-decoration: underline;
        
        &:hover {
          color: $color-light;
          text-decoration: underline;
        }
      }
    }
  }
  &-link {
    display: inline-block;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.3px;
    text-decoration-line: underline;
    text-transform: uppercase;
    padding: 0 4px;
    margin-left: -4px;
    border-radius: 4px;
    color: #fff;

    &:hover {
      color: #fff;
      background-color: $color-red;
      text-decoration: underline;
    }
  }
  &-userinfo {
    margin-bottom: 32px;

    &-label {
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      margin-bottom: 4px;
    }
    &-phone {
      display: flex;
      align-items: center;
      margin-bottom: 4px;

      &-flag {
        flex: 0 0 24px;
        max-width: 24px;
        margin-right: 8px;

        img {
          display: block;
          width: 100%;
        }
      }
      &-text {
        font-family: $font-default;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        padding-bottom: 3px;
      }
    }
  }
  &-content {
    font-family: $font-PN;
    font-size: 16px;
    line-height: 1.4;
    color: #fff;
  }

  &-form {
    position: relative;
    margin-bottom: 24px;

    &-body {
      display: flex;
      flex-wrap: wrap;
    }
    &-item {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }
    &-input {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;

      @media #{$mmedia-sm} {
        width: 100%;
      }
    }
    &-btn {
      flex: 0 0 191px;
      max-width: 191px;
      margin-left: 16px;

      @media #{$mmedia-sm} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-left: 0;
        margin-top: 16px;
      }
    }
    &-info {
      position: relative;
      margin-top: -12px;
      font-size: 16px;
      line-height: 24px;

      @media #{$mmedia-sm} {
        font-size: 14px;
        line-height: 20px;
      }

      a {
        display: inline-block;
        color: #fff;
        text-decoration: underline;

        &:hover {
          color: $color-light;
          text-decoration: underline;
        }
      }
      div {
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .forminput {
      margin-top: -9px;
    }
    .iti__country-list {
      color: $color-default;
    }
  }
}

.mclose {
  position: absolute;
  right: 16px;
  top: 20px;
  width: 40px;
  height: 40px;
  outline: none !important;
  padding: 0;
  border: none;
  mask-repeat: no-repeat !important;
  mask-position: center center !important;
  mask-size: contain !important;
  mask-image: url('../images/ic/mclose.svg');
  background-color: $color-line;
  z-index: 10;
  
  &:hover {
    background-color: #fff;
  }
}