.searchmobile {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 201;
  overflow: hidden;
  transform: translateX(100%);
  transition: transform 0.3s;

  @media #{$mmedia-mmd} {
    display: none !important;
  }

  &.active {
    transform: translateX(0);
  }
  &-body {
    position: absolute;
    left: 60px;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 16px;
    background-color: #fff;
    box-shadow: -10px 0px 20px rgba(0,0,0,.4);
    overflow-x: hidden;
    overflow-y: auto;
  }

  .search-pcontent {
    padding: 0;
  }
  .search-pfoot {

  }
}