html.smooth-scroll {
  overflow: hidden;
  height: 100%;
  //margin-right: calc(-1 * var(--scrollbarWidth));
  -webkit-overflow-scrolling: touch;

  body {
    //height: 100%;
    padding-top: 0 !important;
    padding-right: 0 !important;

    .header {
      margin-right: 0 !important;
    }

    .page .main-content {
      padding-top: 200px;

      @media #{$mmedia-md} {
        padding-top: 88px;
      }
    }
  }

  &.fixscroll {
    .scroll-content {
      //max-height: 100%;
    }
  }

  .mfp-wrap {
    z-index: 20;
  }

  .mfp-container {
    .scroll-content {
      display: inline-block;
      vertical-align: middle;
      width: 100%;
    }
  }
}

.scroll-v1 {
  .scrollbar-track {
    @include transition_all(width .3s);
    z-index: 15;
    background: rgba(255, 255, 255, 0) !important;
    //box-shadow: 0 0 1px rgba(0, 0, 0, .15);

    .scrollbar-thumb {
      width: inherit;
      height: inherit;
    }

    &.scrollbar-track-y {
      width: 4px;

      &:hover {
        width: 8px;
      }

      .scrollbar-thumb {
        right: 0;
        left: auto;

        &:focus {
          width: 8px;
        }
      }
    }

    &.scrollbar-track-x {
      height: 4px;

      &:hover {
        height: 8px;
      }

      .scrollbar-thumb {
        bottom: 0;
        top: auto;

        &:focus {
          height: 8px;
        }
      }
    }

    .scrollbar-thumb {
      width: inherit;
      height: inherit;
    }

    .fixscroll.smooth-scroll & {
      display: none !important;
    }

    &:hover {
      width: 8px;
    }
  }

  .scrollbar-thumb {
    cursor: pointer;
    background: $color_red;
  }
}

.blockscroll-fix {
  .scroll-content {
    padding-bottom: 21px;
  }
}
