*,
*::after,
*::before {
  box-sizing: border-box;
  border-collapse: collapse;
  outline: 0;
}

a,
li,
img,
button,
input {
  transition: all 0.3s ease;
}

input[type='submit'],
button {
  cursor: pointer;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
  appearance: none;
  box-sizing: border-box;
  background-color: #fff;
  background-image: none;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0;
  border-radius: 0;
  text-align: left;
}

@-webkit-keyframes autofill {
  to {
    color: inherit;
    background: transparent;
  }
}

input:-webkit-autofill {
  -webkit-animation-name: autofill;
  -webkit-animation-fill-mode: both;
}

button {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}

$develop: false;
$production: true;
$all: true;
$ie9: false;
$ie8: false;
$ie7: false;
$doc: false;
$local_var: global;
$holydat_var: none;

$color-default: #2c2c2c;
$translucent-default: rgba(44, 44, 44, 1);
$color-dark: #000;
$color-white: #f5f5f5;
$color-red: #e62739;
$color-redlight: #ec6672;
$color-pink: #fce4e6;
$color-redshadow: #e62739;
$translucent-red: rgba(230, 39, 57, 1);
$color-grey: #4f4f4f;
$color-light: #a1a1a1;
$color-dgrey: #2c2c2c;
$color-line: #cdcdcd;
$color-green: #3ebd6b;
$color-greenlight: #66d98f;
$color-bggreen: #ecf8f0;

$mmedia-mlg: (min-width: 1200px);
$mmedia-lg: (max-width: 1199px) or (-webkit-device-pixel-ratio: 2) or (-webkit-device-pixel-ratio: 3);
$mmedia-mmd: (min-width: 992px);
$mmedia-md: (max-width: 991px) or (-webkit-device-pixel-ratio: 2) or (-webkit-device-pixel-ratio: 3);
$mmedia-msm: (min-width: 768px);
$mmedia-sm: (max-width: 767px) or (-webkit-device-pixel-ratio: 2) or (-webkit-device-pixel-ratio: 3);
$mmedia-mmm: (min-width: 575px);
$mmedia-mm: (max-width: 575px) or (-webkit-device-pixel-ratio: 2) or (-webkit-device-pixel-ratio: 3);
$mmedia-mxm: (min-width: 375px);
$mmedia-xm: (max-width: 374px);

// all pages
@import "include/globals";
@import "include/font";
@import "../smarket_icons/style.scss";
//@import "../icomoon-font-ak/style.scss";
@import "include/effects";
@import "include/scroll";

html {
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background: $color-dark;
    border-radius: 4px;
  }

  &.fixscroll {
    @media #{$mmedia-md} {
      overflow: hidden !important;

      body {
        //overflow: hidden !important;
      }
    }
  }

  &.fixscrollsearch {
    overflow: hidden !important;

    body {
      overflow: hidden !important;
    }
  }

  &.smactive {
    overflow-y: hidden !important;

    header,
    body {
      @media only screen {
        padding-right: var(--scrollbarWidth);
      }
    }

    .header-body::before {
      opacity: 0;
    }

    .header-logo a {
      height: 66px;

      img {
        width: 75px;
      }
    }
  }
}

body {
  font-family: $font-default;
  font-size: 18px;
  line-height: 1.78;
  color: $color-default;
  padding-top: 176px;

  &.header-v2 {

    .globalmenu {
      display: none !important;
    }

    @media #{$mmedia-mmd} {
      padding-top: 200px;
    }
  }

  @media #{$mmedia-md} {
    font-size: 16px;
    line-height: 1.75;
    padding-top: 88px;
  }

  .overlayLoader {
    display: none;
  }

  &.modal-open {
    @media only screen and (min-width: 992px) {
      padding-right: 8px !important;
    }

    .header {
      @media only screen and (min-width: 992px) {
        margin-right: 8px;
      }
    }

    .cookieapply {
      padding-right: 8px;
    }
  }

  &.scroll-down {
    .header {
      transform: translateY(-100%);
    }

    .header-deskmbtn {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }

    .filter .layoutwhite {
      top: 0;
    }
  }

  &.scroll-up {
    .header {
      transform: translateY(0);
    }

    .header-deskmbtn {
      transform: translateY(-100%);
      opacity: 0;
      //visibility: hidden;
    }

    &.bodypcard {
      @media #{$mmedia-mmd} {
        .header {
          transform: translateY(-100%);
        }
        .header-deskmbtn {
          transform: translateY(0);
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }

  a {
    outline: none !important;
    color: $color-default;
    text-decoration: underline;

    &:focus,
    &:active {
      outline: none !important;
    }

    &:hover {
      color: $color-dark;
      text-decoration: none;
    }
  }

  p {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .h1, h1 {
    font-family: $font-PNC;
    font-size: 64px;
    font-weight: 900;
    line-height: 1;
    color: $color-grey;
    padding: 0;
    margin: 0 0 32px 0;

    @media #{$mmedia-lg} {
      font-size: 56px;
    }
    @media #{$mmedia-md} {
      font-size: 48px;
    }
    @media #{$mmedia-sm} {
      font-size: 32px;
    }
  }

  .h2, h2 {
    font-family: $font-PNC;
    font-size: 48px;
    line-height: 1;
    font-weight: normal;
    letter-spacing: -0.75px;
    padding: 0;
    margin: 0 0 24px 0;

    @media #{$mmedia-md} {
      font-size: 32px;
    }

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .h3, h3 {
    font-family: $font-PNC;
    font-size: 32px;
    line-height: 1;
    font-weight: normal;
    letter-spacing: -0.75px;
    padding: 0;
    margin: 0 0 16px 0;

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .h4, h4 {
    font-family: $font-default;
    font-size: 24px;
    line-height: 1.4;
    font-weight: bold;
    padding: 24px 0 0 0;
    margin: 0 0 8px 0;

    @media #{$mmedia-md} {
      font-size: 20px;
    }
    @media #{$mmedia-sm} {
      font-size: 16px;
    }

    &:first-child {
      padding-top: 0;
    }
  }

  .h5, h5 {
    font-family: $font-PN;
    font-size: 14px;
    line-height: 1.43;
    font-weight: bold;
    padding: 16px 0 0 0;
    margin: 0 0 4px 0;
    text-transform: uppercase;

    &:first-child {
      margin-top: 0;
      padding-top: 0;
    }

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .row {
    margin-left: -16px;
    margin-right: -16px;

    & > div {
      padding-left: 16px;
      padding-right: 16px;
    }

    &.no-gutters {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .container, .container-fluid {
    padding-left: 16px;
    padding-right: 16px;
  }

  form, .form {

    small {
      display: block;
      font-family: $font-PN;
      font-size: 16px;
      line-height: 24px;
      margin-top: 4px;

      a {
        display: inline-block;
        text-decoration: underline;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }
      }

      p {
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      & + div {
        padding-top: 8px;
      }
    }

    &-group {
      margin-bottom: 16px;

      &-lg {
        margin-bottom: 24px;
      }
    }

    &-minfo {
      font-style: italic;
      font-weight: 300;
      font-size: 16px;
      line-height: 28px;

      a {
        text-decoration: underline;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }
      }
    }

    &-supinfo {
      padding: 8px;
      background-color: $color-white;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      border-radius: 4px;
      max-width: 397px;

      img {
        display: inline-block;
        margin-left: 4px;
        margin-right: 4px;
        vertical-align: middle;
      }
    }
  }
}

main {
  position: relative;
  overflow: hidden;
  z-index: 10;

  &.mainfilter {
    z-index: 10;
  }
}

.iti__flag {
  background-image: url("../images/system/flags.png");
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .iti__flag {
    background-image: url("../images/system/flags@2x.png");
  }
}

.layoutwhite {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 199;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s linear;
  opacity: 0;
  visibility: hidden;

  &.active {
    visibility: visible;
    opacity: 1;
  }
}

.headerlayout {
  position: fixed;
  top: 136px;
  left: 0;
  z-index: 200;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s linear;
  opacity: 0;
  visibility: hidden;

  @media #{$mmedia-md} {
    top: 88px;
  }

  &.active {
    visibility: visible;
    opacity: 1;
  }
}

.pcard {
  position: relative;
  padding: 28px 0 0 0;
  transition: all 0.3s ease;
  font-family: $font-PN;
  width: 100%;
  z-index: 5;

  &.pcard-theme {
    height: 100%;

    .pcard-popup {
      top: -3px;
      bottom: -34px !important;
      opacity: 1;
      visibility: visible;
      border-radius: 0;
      box-shadow: 0 0 1px #cdcdcd;
    }
  }

  @media #{$mmedia-md} {
    padding-top: 0;
  }

  &-popup {
    position: absolute;
    left: -16px;
    top: -16px;
    right: -16px;
    bottom: -56px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    box-shadow: 0 8px 16px transparentize($translucent-default, 0.9);
    background-color: #fff;
    opacity: 0;
    border-radius: 4px;
    visibility: hidden;
    transition: all .3s ease;
    animation-delay: .1s;
    z-index: -1;

    @media #{$mmedia-md} {
      display: none !important;
    }

    .mbtn {
      border-radius: 0 0 4px 4px;
    }
  }

  &:hover {
    z-index: 6;

    .pcard-popup {
      bottom: -70px;
      opacity: 1;
      visibility: visible;
    }

    .pcard-name, .pcard-text {
      color: $color-red;
    }
  }

  &-img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 256px;
    margin-bottom: 12px;
    position: relative;

    @media #{$mmedia-md} {
      height: 192px;
    }

    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &-info {
    display: flex;
    align-items: center;
    position: relative;
    list-style: none;
    margin: -5px;
    padding: 0;

    li {
      padding: 5px;
    }
  }

  &-price {
    font-family: $font-PNC;
    font-size: 48px;
    color: $color-grey;
    line-height: 1;
    height: 48px;
    letter-spacing: -2px;
    white-space: nowrap;

    @media #{$mmedia-md} {
      font-size: 32px;
      height: 32px;
      letter-spacing: -.4px;
    }
  }

  &-priceold {
    font-family: $font-PNC;
    font-size: 24px;
    font-weight: 300;
    line-height: 1;
    color: $color-light;
    letter-spacing: -.4px;
    text-decoration-line: line-through;
    white-space: nowrap;

    @media #{$mmedia-md} {
      font-size: 18px;
      letter-spacing: normal;
    }
  }

  &-simushka {
    display: block;
    border: 2px solid $color-red;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    position: relative;
    text-align: center;
    font-family: $font-PN;
    line-height: 24px;
    font-size: 18px;
    color: $color-red;
    font-weight: bold;

    @media #{$mmedia-md} {
      width: 20px;
      height: 20px;
      line-height: 16px;
      font-size: 13px;
    }
  }

  &-name {
    font-size: 16px;
    font-weight: bold;
    color: $color-default;
    line-height: 1.5;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;

    @media #{$mmedia-md} {
      font-size: 16px;
    }
  }

  &-text {
    display: block;
    font-size: 16px;
    line-height: 1.25;
    margin-bottom: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-decoration: none;
    transition: all .3s ease;

    @media #{$mmedia-md} {
      font-size: 14px;
    }

    @media #{$mmedia-md} {
      margin-bottom: 8px;
    }

    &:hover {
      color: $color-red;
    }
  }

  &-bonus {
    overflow: hidden;
    height: 24px;
    font-family: $font-PN;
    margin-top: 16px;
    position: relative;
    text-decoration: none;
    display: flex;

    @media #{$mmedia-md} {
      margin-top: 8px;
    }

    &:hover {
      opacity: .8;
    }

    & > div:first-child {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }

    & > div:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }

    &-link {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
    }

    &-icon {
      flex: 0 0 24px;
      max-width: 24px;
      background: $color-red url('../images/ic/grafup.svg') 50% 50% no-repeat;
    }

    &-info {
      font-size: 14px;
      line-height: 24px;
      font-weight: bold;
      color: #fff;
      background-color: $color-green;
      padding: 1px 30px 0 8px;
      transition: all 0.3s ease;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        right: 8px;
        top: 4px;
        width: 16px;
        height: 16px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: auto 100% !important;
        mask-image: url('../images/ic/ic-smoney.svg');
        background-color: #fff;
      }
    }
  }

  &-volume {
    list-style: none;
    margin: 12px -4px -4px -4px;
    padding: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    overflow: hidden;

    @media #{$mmedia-md} {
      margin: 6px -2px -2px -2px;
    }

    li {
      display: block;
      padding: 4px;

      @media #{$mmedia-md} {
        padding: 2px;
      }
    }

    a {
      display: block;
      border: 1px solid transparent;
      border-radius: 4px;
      padding: 4px 4px 3px 4px;
      font-size: 14px;
      font-weight: 300;
      line-height: 1;
      color: $color-light;
      white-space: nowrap;
      text-decoration: none;

      &:hover {
        color: $color-red;
      }

      &.active {
        border-color: $color-light;

        &:hover {
          border-color: $color-red;
          color: $color-red;
        }
      }
    }
  }

  &-article {
    font-size: 14px;
    line-height: 20px;
    color: $color-grey;
    margin-top: 8px;

    @media #{$mmedia-sm} {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &-check {
    position: absolute;
    padding: 0;
    margin: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    z-index: 10;

    input[type="checkbox"] {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;

      &:checked {
        & ~ .pcard-check-bg {
          opacity: .8;
        }

        & ~ .pcard-check-body {
          opacity: 1;
        }
      }
    }

    &-body {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      color: #fff;
      font-family: $font-PN;
      text-align: center;
      opacity: 0;
      transition: all 0.3s ease;
      z-index: 2;
    }

    &-bg {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: #3ebd6b;
      opacity: 0;
      transition: all 0.3s ease;
      z-index: 1;
    }

    &-number {
      width: 201px;
      height: 201px;
      padding-top: 18px;
      border: 8px solid #fff;
      border-radius: 50%;
      margin: 0 auto 24px auto;
      font-family: $font-PNC;
      font-size: 144px;
      line-height: 175px;
      text-align: center;
      letter-spacing: -2px;

      @media #{$mmedia-md} {
        width: 112px;
        height: 112px;
        margin-bottom: 32px;
        font-size: 72px;
        line-height: 88px;
        padding-top: 10px;
        letter-spacing: -2px;
      }
    }

    &-link {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.3px;
      text-decoration-line: underline;
      text-transform: uppercase;
    }
  }

  &-noprice {
    font-family: $font-PNC;
    font-size: 24px;
    line-height: 24px;
    color: $color-light;
  }

  .lineinfo {
    position: absolute;
    left: 0;
    top: 0;
    max-width: 100%;
    z-index: 4;

    @media #{$mmedia-md} {
      transform: rotate(-90deg) translateX(-100%);
      margin-left: -4px;
      transform-origin: left top;
    }
  }
}

.forminput {
  display: block;
  position: relative;
  padding: 9px 0 0 0;
  margin: 0;
  font-family: $font-PN;
  font-size: 16px;
  line-height: 24px;
  cursor: text;

  .temp_flag {
    position: absolute;
    left: 14px;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 5px;
    width: 24px;
    height: auto;
    opacity: 0;
    z-index: -1;
  }

  &.forminput-lg {
    &:not(.active) {
      .forminput-input {
        background: none !important;
        background-image: none !important;
      }
    }

    &.active {
      .forminput-label {
        font-size: 12px;
        line-height: 16px;
        top: 1px;
      }

      .forminput-input {
        &.phone {
          padding-left: 54px;
          background-repeat: no-repeat;
          background-position: 14px 50%;
          background-size: 24px auto;
        }
      }
    }

    .forminput-label {
      font-size: 18px;
      line-height: 28px;
      top: 23px;

      @media #{$mmedia-sm} {
        font-size: 16px;
        line-height: 24px;
        top: 20px;
      }
    }

    .forminput-input {
      padding: 13px 54px 13px 16px;
      font-size: 18px;
      line-height: 28px;

      @media #{$mmedia-sm} {
        font-size: 16px;
        line-height: 24px;
        padding-top: 11px;
        padding-bottom: 11px;
      }

      &.phone {
        padding-left: 54px;
      }

      &:-webkit-autofill {
        background: none !important;
        color: $color-default !important;
      }
    }

    .search-status {
      top: 9px;

      @media #{$mmedia-sm} {
        top: 5px;
      }
    }
  }

  &.forminput-dark {
    &.active {
      .forminput-label {
        color: $color-line;
        background-color: $color-default;
      }

      .forminput-input {
        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus,
        &:-webkit-autofill:active {
          background-color: transparent !important;
          color: $color-default !important;
          -webkit-text-fill-color: $color-default !important;
          -webkit-box-shadow: inset 0 0 0 50px rgba(255, 255, 255, 0) !important;

          @media #{$mmedia-md} {
            color: #fff !important;
            -webkit-text-fill-color: #fff !important;
          }
        }
      }
    }

    .forminput-label {
      background-color: $color-dgrey;
    }

    .forminput-input {
      color: #fff;
      background-color: $color-dgrey;
      border-color: $color-white;
    }

    .sicon.delete {
      background-color: #4f4f4f;

      &:hover {
        background-color: #e62739;
      }
    }
  }

  &-label {
    color: $color-light;
    position: absolute;
    left: 16px;
    top: 21px;
    transition: all 0.3s ease;
    z-index: 5;
  }

  &-input {
    display: block;
    width: 100%;
    border: 1px solid $color-grey;
    border-radius: 4px !important;
    background-color: #fff;
    padding: 12px 40px 12px 16px;
    font-size: 16px;
    font-weight: normal;
    line-height: 24px;
    outline: none;
    color: #4f4f4f;

    &:required ~ .search-status .search-sup {
      display: flex;
    }

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active {
      background-color: transparent !important;
      color: $color-default !important;
      -webkit-text-fill-color: $color-default !important;
      -webkit-box-shadow: inset 0 0 0 50px rgba(255, 255, 255, 0) !important;
    }

    &[type=number] {
      -moz-appearance: textfield;
    }

    &.ordermask {
      padding-left: 41px !important;
    }
  }

  .ordermask-mask {
    position: absolute;
    left: 0;
    top: 10px;
    font-size: 16px;
    font-weight: normal;
    line-height: 24px;
    padding: 16px 0 14px 16px;
    opacity: 0;
    color: $color-default;
    z-index: 3;
  }

  .search-status {
    top: 6px;
  }

  &-popup {
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    padding-top: 8px;
    display: none;

    .nano {
      max-height: 260px;
    }

    & > div {
      border: 1px solid $color-red;
      background-color: $color-default;
      color: #fff;
      padding: 20px 16px;
      box-shadow: 0 8px 16px rgba(44, 44, 44, .1);
      border-radius: 4px;
    }
  }

  &-list {
    list-style: none;
    margin: 0;
    padding: 0;
    font-family: $font-PN;
    max-width: 100%;

    & > li {
      padding-top: 4px;
      margin-top: 4px;
      border-top: 1px solid #fff;

      &:first-child {
        padding-top: 0;
        margin-top: 0;
        border: none;
      }

      & > a {
        display: block;
        color: $color-white;
        text-decoration: none;

        &:hover {
          color: $color-red;
        }

        b {
          font-weight: bold;
        }

        span {
          display: block;
          font-size: 18px;
          line-height: 28px;
        }

        p {
          padding: 0;
          margin: 0;
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
  }

  &.active {
    .forminput-label {
      top: 0;
      padding-left: 4px;
      padding-right: 4px;
      background-color: #fff;
      font-size: 12px;
      line-height: 16px;
      color: $color-grey;
    }

    .forminput-input {
    }

    .ordermask-mask {
      opacity: 1;
    }

    &.error {
      .forminput-label {
        padding-left: 16px;
        color: $color-red;

        &::after {
          content: '';
          position: absolute;
          top: 4px;
          left: 4px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: $color-red;
        }
      }

      .forminput-input {
        border-color: $color-red;
        color: $color-red;
      }

      .search-status .search-sup {
        display: none;
      }

      .ordermask-mask {
        color: $color-red;
      }
    }

    &.valid {
      .forminput-label {
        padding-left: 16px;
        color: $color-green;

        &::after {
          content: '';
          position: absolute;
          top: 4px;
          left: 4px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: $color-green;
        }
      }

      .forminput-input {
        border-color: $color-green;
        color: $color-green;
      }

      .search-status .search-sup {
        display: none;
      }
    }

    .temp_flag {
      opacity: 1;
      z-index: 2;
    }
  }
}

.section-controls {
  margin-bottom: 85px;
}

.mb-24 {
  margin-bottom: 24px !important;
}

.mbtn-value {
  font-style: normal;
}

