html {

  &.ios {
    overflow-y: scroll !important;
    -webkit-overflow-scrolling: touch;
  }

  @include iphone-only() {
    &:before {
      content: 'iphone';
      position: absolute;
      top: -100px;
      left: -100px;
    }
  }
}

.header {

  .scroll-up.header-v2 & {
    transition-delay: .15s;

    @media #{$mmedia-mmd} {

    }
  }

  &-btnmenu {
    display: block;
    outline: none !important;
    border: none;
    padding: 0;
    width: 60px;
    height: 64px;
    background-color: $color-default;
    text-decoration: none;
    color: #fff;
    position: relative;
    cursor: pointer;
    margin-left: 12px;

    span {
      position: absolute;
      left: 16px;
      width: 28px;
      height: 2px;
      border-radius: 2px;
      background-color: #fff;
      transition: all 0.3s ease;
      z-index: 1;

      &.l00 {
        top: 21px;
      }

      &.l01 {
        top: 31px;
      }

      &.l02 {
        top: 41px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      left: 37px;
      top: 37px;
      width: 13px;
      height: 13px;
      border: 2px solid $color-default;
      background-color: $color-red;
      border-radius: 50%;
      display: none;
      z-index: 3;
    }

    &.inlogin::after {
      display: block;
    }

    &.active {
      background-color: $color-red;

      span {
        &.l00 {
          top: 30px;
          transform: rotate(45deg);
        }

        &.l01 {
          opacity: 0;
        }

        &.l02 {
          top: 30px;
          transform: rotate(-45deg);
        }
      }

      &.inlogin::after {
        opacity: 0;
      }
    }
  }

  .header-v2 & {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    transform: translateX(0);
    transition: transform 0.3s;
    z-index: 200;
    padding-bottom: 20px;
    font-family: $font-PN;

    &.openmenu {
      @media #{$mmedia-md} {
        transform: translate(calc(-100% + 60px), 0);
      }
    }

    @media #{$mmedia-mmd} {
      padding-bottom: 40px;
      transform: translateX(0);
    }

    .header-btnmenu {
      margin-left: 5px;

      &:not(.active) {
        background: #000;
      }
    }

    .header-top:first-child {
      background-color: #000;
      color: #fff;
      position: relative;

      @media #{$mmedia-md} {
        > .container {
          max-width: none;
        }
      }

      .container {
        //@include transition();

        @media #{$mmedia-md} {
          padding: 0;

          &.education-holder {
            padding: 0 8px;
          }
        }
      }

      @media #{$mmedia-md} {

      }

      @media #{$mmedia-mmd} {
        //padding-bottom: 40px;

        //&::before {
        //content: '';
        //position: absolute;
        //bottom: 0;
        //right: 0;
        //left: 0;
        //background: $color-red;
        //height: 40px;
        //}

        //&::after {
        //  content: '';
        //  position: absolute;
        //  bottom: -30px;
        //  right: 0;
        //  left: 0;
        //  background: #fff;
        //  height: 30px;
        //}
      }
    }

    .header-btnmenu {
      &.active {
        span {
          width: 20px;
          left: 19px;
        }
      }

      &:not(.active) {
        span {
          width: 24px;

          &.l00 {
            top: 23px;
          }

          &.l02 {
            top: 39px;
          }
        }
      }
    }

    &-deskmbtn {
      opacity: 0;
      transition: transform 0.2s ease, opacity 0.2s ease;
      visibility: visible;
      position: fixed;
      transform: translateY(-150%);
      left: 50%;
      margin-left: -24px;
      top: 16px;
      z-index: 250;
      width: 48px;
      height: 48px;
      box-shadow: 0 0 30px rgba(79, 79, 79, .2);
      background: $color-red url(../images/system/smarket.svg) 50% 50% no-repeat;
      background-size: 66%;
      border-radius: 50% 50%;
    }
  }

  .menu-opened & {

    .header-top:first-child {
      @media #{$mmedia-md} {
        .container {
          max-width: 100vw;
        }
      }
    }
  }
}

.scroll-down.header-v2 {
  .header-deskmbtn {
    transition-delay: .25s;

    @media #{$mmedia-mmd} {
    }
  }
}

.layoutglobal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 199;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s linear;
  opacity: 0;
  visibility: hidden;

  &.active {
    visibility: visible;
    opacity: 1;
  }
}

.layoutglobal,
.layoutwhite {
  .header-v2 & {
    z-index: 12;
    height: auto;
    bottom: 0;
  }

  .menu-opened.header-v2 & {
    @media #{$mmedia-mmd} {
      display: none !important;
    }
  }
}

.search-mobileclose,
.mfilter-mobileclose,
.mflcatalog-mobileclose {
  .header-v2 & {
    background-size: 25%;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      height: var(--currentVH);
      left: 0;
      right: 0;
      opacity: 0 !important;
    }
  }
}
