.mbtn {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  text-decoration: none;
  outline: none !important;
  background: none;
  font-family: $font-PN;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  text-transform: uppercase;
  padding: 14px;
  text-align: center;
  border: 2px solid transparent;
  border-radius: 4px;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;

  @media #{$mmedia-sm} {
    font-size: 14px;
  }

  &.mbtn-xs {
    min-width: 0;
    padding: 1px 7px;
    font-size: 12px;
  }

  &.mbtn-sm {
    min-width: 0;
    min-height: 32px;
    padding: 4px 7px;
    font-size: 12px;
  }

  &.mbtn-lg {
    font-size: 18px;
    padding-top: 21px;
    padding-bottom: 20px;
  }

  &.mbtn-xlg {
    font-size: 18px;
    line-height: 1;
    min-height: 80px;
    letter-spacing: .017em;

    span {
      &[class^="icon-"], &[class*=" icon-"] {
        font-size: 1.3em;
      }
    }
  }

  &-primary {
    color: $color-white;
    background-color: $color-red;
    border-color: $color-red;
    box-shadow: 0 8px 16px rgba(0, 0, 0, .2);

    @media #{$mmedia-mmd} {
      &:hover {
        color: $color-white;
        background-color: $color-redlight;
        border-color: $color-redlight;
      }
    }

    &:disabled {
      background-color: $color-light;
      border-color: $color-light;
      box-shadow: none;
      cursor: default;
    }

    &:visited, &:focus {
      color: $color-white;
      background-color: $color-red;
      border-color: $color-red;
    }

    &-outline {
      border-radius: 0;
      border: .1rem solid $color-red;
      font-size: 16px;
      color: $color-red;
      text-transform: uppercase;
      letter-spacing: 0;

      &:hover,
      &:focus {
        background-color: $color-red;
        color: #fff;
      }
    }
  }

  &-green {
    color: $color-white;
    background-color: $color-green;
    border-color: $color-green;
    box-shadow: 0 8px 16px rgba(0, 0, 0, .2);

    &:hover {
      color: $color-white;
      background-color: $color-greenlight;
      border-color: $color-greenlight;
    }

    &:disabled {
      background-color: $color-light;
      border-color: $color-light;
      box-shadow: none;
      cursor: default;
    }
  }

  &-secondary {
    color: $color-white;
    background-color: $color-dgrey;
    border-color: $color-dgrey;
    box-shadow: 0 8px 16px rgba(0, 0, 0, .2);

    &:hover {
      color: $color-white;
      background-color: $color-grey;
      border-color: $color-grey;
    }

    &:disabled {
      background-color: $color-light;
      border-color: $color-light;
      box-shadow: none;
      cursor: default;
      box-shadow: none;
    }
  }

  &-white {
    color: $color-red;
    background-color: #fff;
    border-color: #fff;
    box-shadow: 0 8px 16px rgba(0, 0, 0, .2);

    &:hover {
      color: $color-red;
      background-color: $color-pink;
      border-color: $color-pink;
    }

    &:disabled {
      color: $color-red;
      background-color: $color-pink;
      border-color: $color-pink;
      box-shadow: none;
      cursor: default;
    }
  }

  &-whitetgrey {
    color: $color-grey;
    background-color: #fff;
    border-color: #fff;
    box-shadow: 0 8px 16px rgba(0, 0, 0, .2);

    &:hover {
      color: $color-grey;
      background-color: $color-white;
      border-color: $color-white;
    }

    &:disabled {
      color: $color-grey;
      background-color: $color-white;
      border-color: $color-white;
      box-shadow: none;
      cursor: default;
    }
  }

  &-greytpink {
    color: $color-default;
    background-color: $color-white;
    border-color: $color-white;
    box-shadow: none;

    &:hover {
      color: $color-default;
      background-color: $color-pink;
      border-color: $color-pink;
    }

    &:disabled {
      color: $color-grey;
      background-color: $color-white;
      border-color: $color-white;
      box-shadow: none;
      cursor: default;
    }
  }

  &-blacktgrey {
    color: #fff;
    background-color: #000;
    border-color: #000;
    box-shadow: none;

    &:hover {
      color: #fff;
      background-color: $color-default;
      border-color: $color-default;
    }

    &:disabled {
      color: $color-line;
      background-color: $color-light;
      border-color: $color-light;
      box-shadow: none;
      cursor: default;
    }
  }

  .sicon, span {
    vertical-align: middle;
    line-height: 1;
  }

  > span + span,
  > span + .sicon {
    margin-left: 10px;
  }

  > .sicon + span {
    margin-right: 10px;
  }
}

.d-flex.mbtn {
  flex-flow: row nowrap;
  align-items: center;
}
