$show-menu-hover: true; // true for mobile
$show-menu-active: true; // false for desktop

$mobMenuTransitionTime: .5s;
$dropdownDelay: .15s;

@mixin activeMenuItems {
  @include transition_all(transform $mobMenuTransitionTime, top $mobMenuTransitionTime);
  position: absolute;
  left: 0;
  width: 100%;

  @for $i from 0 through 40 {
    &:nth-child(#{$i}) {
      top: 64px + ($i - 1) * 56px;
    }
  }

  &:not(.active) {
    //display: none;
    transform: translateX(-100%);

    &::after {
      background: #fff;
    }

    > .navbar-megamenu_link:after {
      border-left-width: 0;
    }
  }
}

@mixin sub_menu_arrow {
  @include transition_all(border .2s, left .2s);
  content: '';
  position: absolute;
  left: 100%;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 6px solid $color-default;
}

@mixin item_borders {
  @include transition_all(transform $mobMenuTransitionTime);
  position: relative;

  &::after {
    @include transition_all(background .3s);
    content: '';
    position: absolute;
    top: -1px;
    right: 8px;
    left: 8px;
    height: 1px;
    background: $color-grey;
  }

  &.__no-dbr,
  &.active {
    &::after {
      display: none;
    }
  }

  &:not(.active) {

  }
}

@mixin transIn {
  @media #{$mmedia-md} {
    @include transition_all(transform $mobMenuTransitionTime);
    //@include transition(transform, $mobMenuTransitionTime - .1s);
    transform: translate(100%);

    &.translateIn {
      transform: translate(0);
    }
  }
}

