            }

            &:not(.active) {
              //display: none;
              transform: translateX(-100%);

              > .navbar-submenu_link {
                &::after {
                  border-left-color: transparent !important;
                }
              }
            }
          }
        }
      }

      @media #{$mmedia-mmd} {
        //&.__market {
        //  margin-left: -8px;
        //}
        //
        //&.__education {
        //  margin-left: -24px;
        //}
      }
    }

    &_icon {
      margin-right: 10px;
    }

    &_link {
      @include transition_all(font-size $mobMenuTransitionTime, color $mobMenuTransitionTime, height $mobMenuTransitionTime, padding $mobMenuTransitionTime, box-shadow ($dropdownDelay + $mobMenuTransitionTime) * 2);
      text-transform: uppercase;
      white-space: nowrap;
      text-decoration: none;
      color: #fff;
      display: block;
      height: 40px;
      padding: 10px 18px;
      font-size: 16px;
      line-height: 20px;
      box-shadow: none;

      @media #{$mmedia-mmd} {
        border-radius: 8px 8px 0 0;
      }

      &:hover {
        @media #{$mmedia-mmd} {
          background: #fff;
          //color: $color-red;
        }
      }

      span {
        display: inline-block;
        vertical-align: middle;
        line-height: 1;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &::after {
        @extend %vert_centr;
      }

      @media #{$mmedia-md} {
        @include flex_block(row nowrap, flex-start, center);
        background: #fff;
        height: 56px;
        padding-left: 8px;
        padding-right: 8px;
        color: $color-default;
        position: relative;

        .has-submenu & {
          &::after {
            @include sub_menu_arrow();
            margin-left: -20px;
          }
        }
      }
    }

    &_item {
      &.hover_menu:hover {

        @media #{$mmedia-mmd} {
          ~ .navbar-submenu_dropdown {
            transition-delay: $dropdownDelay;
            height: var(--subMenuHeight);
          }

          ~ .navbar-submenu_overlay {
            opacity: 1;
          }
        }

        .navbar-megamenu_overlay {
          display: block;
        }

        .navbar-submenu_link {
          //background: #fff;
          //color: $color-red;

          @media #{$mmedia-mmd} {
            background: #fff;
            color: $color-red;
            box-shadow: 0 0 32px rgba(0, 0, 0, 0.3);

            &:focus {
              color: $color-default;
            }
          }
        }

        .navbar-megamenu {
          //@include transIn();

          @media #{$mmedia-mmd} {
            //height: 100vh;
            transition-delay: $dropdownDelay;
            opacity: 1;
            pointer-events: all;
            z-index: 12;
          }

          @if ($show-menu-hover) {
            @media #{$mmedia-mmd} {
              display: block;
            }
          }
        }
      }

      &.active {
        position: relative;

        @media #{$mmedia-md} {
          //border-radius: 8px;
          //overflow: hidden;
        }

        .navbar-submenu_link {
          //cursor: default;

          @media #{$mmedia-mmd} {
            //background: #fff;
            //color: $color-default;
            //box-shadow: 0 0 32px rgba(0, 0, 0, 0.3);
          }

          @media #{$mmedia-md} {
            color: #fff;
            //background: $color-grey;
            background-color: transparent;
            height: 64px;
            justify-content: flex-start;
            padding-left: 34px;

            &::after {
              left: 30px;
              border-right: 6px solid #fff;
              border-left: 0;
            }
          }
        }

        .navbar-megamenu {
          @if ($show-menu-active) {
            //display: block;
          }

          @media #{$mmedia-md} {
            // todo unhide
            //display: block;
          }
        }

        @media #{$mmedia-md} {
          > .navbar-megamenu {
            display: block;

            &.translateIn {
              transform: translate(0);
            }
          }
        }
      }

      @media #{$mmedia-md} {
        @include item_borders();
      }
    }
  }

  &-megamenu {

    &_overlay {
      display: none;

      @media #{$mmedia-mmd} {
        @include transition_all(background-color .3s);
        pointer-events: none;
        content: '';
        position: absolute;
        top: 100%;
        margin-top: 8px;
        left: -100vw;
        width: 200vw;
        height: 200vh;
        background-color: rgba(255, 255, 255, 0.7);
      }
    }

    @media #{$mmedia-md} {
      display: none;

      @for $i from 0 through 2 {
        &[data-level="#{$i + 1}"] {
          .col-main {
            .menu_active[data-menu="#{$i + 1}"] {
              > .navbar-megamenu_item {
                @include activeMenuItems();
              }

              > .navbar-megamenu_item.active {
                position: relative;
                top: 0;
                border-radius: 8px;
                overflow: hidden;

                &::before {
                  content: '';
                  position: absolute;
                  top: 0;
                  right: 0;
                  left: 0;
                  height: 64px;
                  border-radius: 8px 8px 0 0;
                  background: $color-grey;
                  z-index: 1;
                }

                > .navbar-megamenu_sub {
                  display: block;
                }

                > .navbar-megamenu_link {
                  color: #fff;
                  //background: $color-grey;
                  background-color: transparent;
                  height: 64px;
                  justify-content: flex-start;
                  padding-left: 34px;
                  z-index: 1;

                  &::after {
                    left: 25px;
                    border-right: 6px solid #fff;
                    border-left: 0;
                  }
                }
              }
            }

            .navbar-megamenu_list[data-menu="#{$i}"] {
              > .navbar-megamenu_item:not(.active) {
                display: none;
              }

              @if ($i > 0) {
                > .navbar-megamenu_item.active {
                  > .navbar-megamenu_link {
                    display: none;
                  }

                  > .navbar-megamenu_sub {
                    display: block;
                  }
                }
              }
            }
          }
        }
      }

      &_inner {
        > .container {
          > .scroll-content {
            > .row.mob-m-0 {
              margin: 0;
            }
          }
        }
      }
    }

    @media #{$mmedia-mmd} {
      @include transition_all(opacity .3s);
      //@include transition(height, .5s);
      color: #000;
      z-index: 1;
      position: fixed;
      left: 0;
      right: 0;
      top: 160px;
      opacity: 0;
      height: 100vh;
      pointer-events: none;
      //height: 0;
      max-height: 455px;
      overflow: hidden;
      //padding: 32px 0;

      &_inner {
        padding: 32px 0;
        height: 100%;
        //max-height: 70vh;

        &.__h-100 {
          //height: 100%;
        }

        .scroll-content {
          @media #{$mmedia-md} {
            background: red;
            transform: translate3d(0px, 0px, 0px) !important;
          }
        }

        > .container {

          @media #{$mmedia-mmd} {
            @include transition_all(height .3s);
            overflow: hidden;
            height: 100%;

            &.__scroll {
              overflow-y: auto;

              &::-webkit-scrollbar {
                width: 4px;
              }

              &::-webkit-scrollbar-thumb {
                background: $color-red;
                border-radius: 2px;
              }
            }

            > .row-reverse {
              height: 100%;
              position: relative;
              flex-flow: row-reverse;
              justify-content: flex-end;

              > .col {
                height: 100%;
                overflow-y: auto;
              }

              .col.__empty + .col.__empty {
                ~ .navbar-megamenu_hero {
                  display: block;
                }
              }
            }
          }

          > .row {
            > .col {
              &::-webkit-scrollbar {
                width: 4px;
              }

              &::-webkit-scrollbar-thumb {
                background: $color-red;
                border-radius: 2px;
              }

              &.__empty {
                display: none;
              }
            }
          }
        }
      }

      &.__middle {
        max-height: 505px;

        .navbar-megamenu_inner {
          padding-bottom: 5px;
        }
      }

      &.__large {
        max-height: calc(40px + var(--subMenuHeight));

        .navbar-megamenu_inner {
          height: auto;

          > .container {
            max-height: 60vh;
          }
        }

        .navbar-megamenu_inner {
          padding-bottom: 5px;
        }
      }

      &.__empty {
        max-height: 8px;
      }

      .auth & {

        .navbar-megamenu_inner {
          //padding-top: 56px;
        }
      }

      .auth.active & {
        //margin-top: -40px;
        //border-top: 8px solid $color-red;

        .navbar-megamenu_inner {
          //padding-top: 56px;
        }
      }

      .auth:hover & {
        opacity: 1;
        //height: 100vh;
      }
    }

    &_hero {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      right: 0;
      display: none;
      pointer-events: none;

      .row {
        justify-content: flex-end;
      }

      .col {
        .navbar-submenu_item:hover & {
          pointer-events: all;
        }
      }

      @media #{$mmedia-md} {
        display: none !important;
      }
    }

    &_list {
      position: relative;
      height: 100%;
    }

    &_link {
      @extend %global_flex_block_row_nowrap_flex_start_center;
      @include transition_all(font-size $mobMenuTransitionTime, color $mobMenuTransitionTime, height $mobMenuTransitionTime, padding $mobMenuTransitionTime);
      height: 56px;
      padding: 16px;
      font-size: 18px;
      line-height: 24px;
      position: relative;
      text-decoration: none;

      span {
        &[class^="icon-"], &[class*=" icon-"] {
          line-height: 1;
        }
      }

      @media #{$mmedia-md} {
        padding-left: 8px;
      }

      @media #{$mmedia-mmd} {
        height: 94px;
        padding: 16px;

        &:hover {
          color: $color-red;

          .navbar-megamenu_item.__sub > & {
            &::after {
              border-left-color: $color-red;
            }
          }

          .navbar-megamenu_count {
            color: $color-red;
            border-color: $color-red;
          }
        }
      }

      span {
        &[class^="icon-"], &[class*=" icon-"] {
          &::before {
            padding-right: 6px;
          }
        }
      }

      &.__brand {
        font-size: 16px;
        height: 56px;

        @media #{$mmedia-mmd} {
          height: 64px;
        }
      }

      &.__fav {
        color: $color-default;
        font-size: 12px;
        height: 58px;
        padding-top: 32px;
        padding-bottom: 0;

        @media #{$mmedia-md} {
          height: 56px;
          padding-top: 30px;
        }
      }
    }

    &_sub {
      @include transIn();
      display: none;

      @media #{$mmedia-mmd} {
        position: absolute;
        top: 0;
        left: 100%;
        width: 100%;
      }

      &.__cloned:first-child {
        display: block;
        left: 0;

        @media #{$mmedia-mmd} {
          padding: inherit;
        }
      }

      .navbar-megamenu_link {
        height: 56px;
        font-size: 16px;
        line-height: 20px;

        @media #{$mmedia-mmd} {
          height: 80px;
          //font-size: 14px;
          //line-height: 18px;
        }
      }
    }

    &_item {
      @media #{$mmedia-mmd} {
        border-top: 1px solid $color-grey;
        border-bottom: 1px solid $color-grey;
        margin-top: -1px;

        &.__no-dbr {
          border-top-color: #fff;
        }

        &:first-child {
          margin-top: 0;
          border-top-color: transparent;
        }
      }

      &.__sub {
        > .navbar-megamenu_link {
          &::after {
            @extend %vert_centr;
            @include sub_menu_arrow();
            margin-left: -5px;

            @media #{$mmedia-md} {
              margin-left: -15px;
            }
          }
        }
      }

      @media #{$mmedia-md} {
        @include item_borders();

        &.active {
          //&::before {
          //  content: '';
          //  position: absolute;
          //  top: 0;
          //  right: 0;
          //  left: 0;
          //  height: 64px;
          //  border-radius: 8px 8px 0 0;
          //  background: $color-grey;
          //  z-index: -1;
          //}
        }
      }

      .__opened + .__opened & {

        @media #{$mmedia-mmd} {
          border-color: transparent;

          > .navbar-megamenu_link {
            color: $color-light;

            &::after {
              border-left: 6px solid $color-light;
              //display: none;
            }
          }
        }

        &.__hovered {
          @media #{$mmedia-mmd} {
            border-color: $color-red;

            > .navbar-megamenu_link {
              color: $color-red;

              &::after {
                display: block;
                border-left: 6px solid $color-red;
              }

              .navbar-megamenu_count {
                color: $color-red;
                border: 1px solid $color-red;
              }
            }
          }
        }
      }

      &:hover {
        @media #{$mmedia-mmd} {
          .navbar-megamenu_sub {
            display: block;
          }
        }
      }
    }

    &_count {
      @include transition();
      //margin-left: 4px;
      margin-right: -8px;
      font: 400 16px/28px $font-PNC;
      color: $color-light;
      min-width: 32px;
      height: 28px;
      text-align: center;
      border-radius: 4px;
      border: 1px solid $color-light;
      margin-left: auto;

      @media #{$mmedia-mmd} {
        margin-right: -16px;
        //width: 40px;
        //height: 40px;
        //line-height: 38px;
      }
    }

    .actions-el {
      margin-top: 0;

      .actions-card {
        position: relative;
        padding: 53px 15px 0;
        margin-top: -15px
      }

      .actions-card-img {
        height: 180px;

        span {
          @extend %hor_centr;
          right: auto;

          img {
            margin: 0 auto;
          }
        }
      }

      .actions-card-name {
        font-size: 32px;
        margin-bottom: 6px;
        -webkit-line-clamp: unset;
      }

      .actions-card-text {
        color: $color-default;
        font: 12px/1.8 $font-default;

        @media #{$mmedia-md} {
          margin-bottom: 0;
          display: block !important;
        }
      }

      .lineinfo-name {
        padding-left: 8px;
        padding-right: 8px;
      }

      @media #{$mmedia-md} {
        margin: 10px 0;

        .actions-card {
          margin-left: -16px;
          margin-right: -16px;

          .webkit-scroll & {
            margin-left: -8px;
            margin-right: -8px;
          }
        }
      }

      @media #{$mmedia-sm} {
        margin-top: 32px;
        margin-bottom: 0;

        .actions-card:before {
          content: '';
          position: absolute;
          top: -1px;
          right: 10px;
          left: 10px;
          border-top: 1px solid $color-grey;

          .webkit-scroll & {
            left: 18px;
            right: 18px;
          }
        }

        &:first-child {
          .actions-card:before {
            display: none;
          }
        }

        &:last-child {
          margin-bottom: 10px;
        }
      }
    }

    .catalogblock-el {

      .pcard-img {
        height: auto;
        margin-bottom: 8px;
      }

      .fz-lg-24 {
        font-size: 24px;
      }
    }
  }
}

